.footer-navigation {
  display: grid;
  grid-template-columns: 220px auto;
  position: relative;
  column-gap: 30px;
  margin-right: 30px;
  list-style-type: none;
  border: none;
  padding: 57px 0;

  @include vp-1023() {
    display: flex;
    flex-direction: column;
    width: 250px;
    margin: 0;
  }

  @include vp-767() {
    width: 100%;
    border-bottom: 1px solid $color-semi-transperent;
    padding: 0 $gap-sm;
  }
}

