.footer-navigation__list li {
  @include vp-1023() {
    margin-bottom: 9px;
  }
}

.footer-navigation__header {
  margin-bottom: 20px;
  font-weight: 400;
  font-style: normal;
  grid-column: 1 / -1;

  @include vp-767() {
    margin-top: 20px;
    margin-bottom: 8px;
  }
}

.footer-navigation__accordeon-button {
  display: none;

  @include vp-767() {
    margin: 0;
    padding: 0;
    top: -25px;
    left: calc(100% - 15px);
    display: block;
    position: relative;
    font-size: 0;
    width: 15px;
    height: 15px;
    background-color: transparent;
    border: none;
  }
}

.footer-navigation__button-sign {
  display: block;
  width: 15px;
  height: 1px;
  background-color: $color-silver;

  &::before {
    content: "";
    display: block;
    width: 15px;
    height: 1px;
    transform: rotate(90deg);
    background-color: $color-silver;
    transition: transform 500ms ease;
  }

  &.is-lost {
    &::before {
      transform: rotate(0deg);
    }
  }
}
